body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.catalog {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  justify-content: flex-end;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link-img {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 10px;
  left: 50px;
}

.nav {
  margin-right: 50px;
  list-style: none;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  transition-duration: .3s;
}

.nav__list {
  margin: 0;
  padding: 0;
  line-height: 60px;
  display: flex;
}

.nav__item {
  text-align: center;
  margin-right: 20px;
  display: inline-block;
}

.nav__item:last-child {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 10px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding-inline: 16px;
  transition: transform .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__title {
  color: #060b35;
  text-align: left;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description {
  text-align: left;
  color: #616070;
  width: 100%;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-top: 16px;
  margin-bottom: 24px;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  display: flex;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 16px 0;
  padding-inline: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
  transition-duration: .3s;
}

.stars {
  flex-direction: row;
  justify-content: space-around;
  width: 96px;
  height: auto;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: 80%;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
